.modal {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
}

.modal__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .8);
    cursor: pointer;
}

.modal__box {
    position: relative;
    width: 80%;
    margin: 0 10%;
    padding: 50px;
    box-sizing: border-box;
    border-radius: 10px;
    background-color: white;
    cursor: auto;
    overflow: auto;

}

.modal__title {
    color: #9E25FC;
    font-size: 30px;
}

.modal__content {
    margin-top: 30px;
    color: #6B6B6B;
    font-size: 16px;
}

.modal__close {
    position: absolute !important;
    top: 20px;
    right: 20px;
    transition: transform 250ms ease-in-out;
    transform-origin: 50% 50%;
}

@media screen and (min-width: 800px) {
    .modal__box {
        max-width: 600px;
        margin: 0 30px;
    }
}